import { emotionslist } from "../../common/chat/emotions";
export default {
  props: {
    isAsk: { type: Boolean, default: !1 },
    showSendImg: { type: Boolean, default: !1 },
    showBulletin: { type: Boolean, default: !1 },
    skin: {
      type: String,
      default: "black"
    }
  },
  data() {
    return {
      baseUrl: this.$store.state.SRC + "/config/app",
      showMore: !1,
      emotionslist: emotionslist,
      isOnlyHost: !1,
      showEmotionList: !1,
      chatTitle: ""
    };
  },
  methods: {
    _setData(data) {
      let keys = Object.keys(data);
      keys.map(k => {
        this[k] = data[k];
      });
    },
    tapOnlyHost() {
      const t = !this.isOnlyHost;
      this._setData({ isOnlyHost: t }),
        this.$emit("onlyhost", { isOnlyHost: t });
    },
    tapEmoji() {
      this._setData({
        showEmotionList: !this.showEmotionList,
        showMore: !1
      });
    },
    tapMore() {
      this._setData({ showMore: !this.showMore, showEmotionList: !1 });
    },
    tapFlower() {
      this.$emit("like");
    },
    chatInputChange(t) {
      const s = t.detail.value;
      this._setData({ chatTitle: s });
    },
    tapSend() {
      let t = this.chatTitle;
      t &&
        t.replace(/\s/g, "") &&
        (this.$emit("sengmsg", { chatTitle: t }),
        this._setData({ chatTitle: "", showEmotionList: !1 }));
    },
    tapEmotionIcon(t) {
      const s = t.currentTarget.dataset.title;
      this._setData({ chatTitle: `${this.chatTitle}[${s}]` });
    },
    tapBulletin() {
      this.$emit("showbulletin");
    }
  }
};
